
import * as cvss from "@/utils/cvss.js";

export default {
  props: {
    value: {
      type: String,
      required: true,
    }
  },
  computed: {
    score() {
      return cvss.scoreFromVector(this.value);
    },
    scoreFormatted() {
      return (this.score || 0).toFixed(1);
    },
    levelName() {
      return cvss.levelNameFromScore(this.score);
    },
    levelNumber() {
      return cvss.levelNumberFromScore(this.score);
    }
  }
}
